import axios from 'axios'
import { baseURL, timeout } from './config'
import { message } from 'antd';
import { getCookie } from '../utils/utils'


let pending = []; //axios
// let cancelToken = axios.CancelToken;
let removePending = (config) => {
    // console.log(config);
    for (let i in pending) {
        if (pending[i].url === axios.defaults.baseURL + config.url) { //
            console.log('-')
            pending[i].f(); //
            //pending.splice(i, 1); pending
            console.log(pending[i].url);
        }
    }
}

export function request(config) {
    const instance = axios.create({
        baseURL,
        timeout,
        headers: {
            'Content-Type': 'application/json',
            'token': getCookie('token') || '',
            'shop_id': localStorage.getItem('shop_id') || '',
        }
    })

    //! 
    instance.interceptors.response.use(res => {
        return res.data
    })

    //
    instance.interceptors.request.use(function (res) {
        removePending(config); //axiosremovePending
        /**
         * @addToken
         */
        return res
    }, function (error) {

        return Promise.reject(error)
    })

    // 
    return new Promise((resolve, reject) => {
        instance(config).then(res => {
            if (res.code !== '0') {
                if (res.code === 'B-009-001-001' || res.code === 'F-000-000-403') {
                    let origin = window.location.origin;
                    window.location.href = `${origin}/integral/login?s=${localStorage.getItem('s')}&overdueToken=1`
                }
                message.destroy();
                setTimeout(() => {
                    message.error({ content: res.msg, duration: 1 })
                }, 200);
                resolve(false)
            } else {
                resolve(res.result)
            }
        }).catch(err => {
            /**
             * @err
             */
            if (err.response) {


            }
        })
    })
}
export default request;
