/* eslint-disable no-undef */
import React from 'react';
import { Route, useHistory } from 'react-router-dom';
import { mapStateToProps, mapDispatchToProps } from '../../redux/actionCreator'
import { connect } from 'react-redux';
import Header from '../header/header';
import './BaseLayout.scss';
import 'animate.css';
import { getInfoApi } from '../../api/api'
//
import router from '../../router/router';
import TabBar from './tabBar/TabBar';
import { useEffect } from 'react';
import { message } from 'antd';
import { getCookie } from '../../utils/utils';

function _Layout(props) {
    const history = useHistory();

    useEffect(() => {
        const p = new URLSearchParams(window.location.search);
        console.log(localStorage.getItem('s'), p.get('s'));

        if (p.get('s')) {  // 
            if (localStorage.getItem('s') !== p.get('s')) {
                props.clearCart();
            }
        }

        if (getCookie('token')) {
            props.setUserInfo();
            if (window.location.pathname === '/integral' || window.location.pathname === '/integral/') { // 
                history.push('/integral/home');
            }
        } else {
            history.replace('/integral/login');
        }
        // getLocation();

        if (navigator.userAgent.toLowerCase().indexOf('micromessenger') !== -1) {
            props.setBrowser('wx');
        } else if (navigator.userAgent.toLowerCase().indexOf('alipayclient') !== -1) {
            props.setBrowser('zfb');
        } else {
            props.setBrowser('other');
        }

    }, [])


    function getPosition() {
        let flag = false;
        return new Promise((resolve, reject) => {
            if (window.location.href.indexOf('https') === -1) { // https
                if (window.location.href.indexOf('localhost') !== -1) {
                    flag = true;
                } else {
                    reject('https');
                    return
                }
            } else {
                flag = true
            }
            if (!flag) return
            if (navigator.geolocation) {
                navigator.geolocation.getCurrentPosition(function (position) {
                    let latitude = position.coords.latitude
                    let longitude = position.coords.longitude
                    let data = {
                        latitude: latitude,
                        longitude: longitude
                    }
                    resolve(data)
                }, function () {
                    reject(arguments)
                })
            } else {
                reject('');
            }
        })
    }
    function getLocation() {
        console.log('start');
        message.loading({
            content: '....', duration: 0,
        })
        // 
        getPosition().then(result => {
            // 
            // {latitude: 30.318030999999998, longitude: 120.05561639999999}
            // 
            let queryData = {
                longtitude: String(result.longitude).match(/\d+\.\d{0,8}/)[0],
                latitude: String(result.latitude).match(/\d+\.\d{0,8}/)[0],
            }
            message.destroy();
            props.setLocaltion(queryData)
            message.success({
                content: '',
                duration: 1,
            });
            // :
            // ...
        }).catch(err => {
            message.destroy();
            message.error(err);
            console.log(err);
        })
    }

    return (
        <div className='layout animate__fadeIn animate__animated'>
            {
                history.location.pathname !== '/integral/order' && history.location.pathname !== '/integral/center' ? <Header /> : null
            }
            {/* <button onClick={() => {
                window.open(locationHref)
            }}>
                
            </button> */}
            { // 
                Object.values(router).map(e => {
                    return (
                        <Route path={e.url} exact component={e.page} key={'/integral' + e.url} />
                    )
                })
            }

            {
                history.location.pathname !== '/integral/home'
                    ? (history.location.pathname !== '/integral/cashier' ? <TabBar /> : null)
                    : (!props.cartSummary.num ? <TabBar /> : null)
            }

        </div>
    )
}
export default connect(mapStateToProps, mapDispatchToProps)(_Layout)