import { message } from "antd";

/**
 * @param {jsx} func 
 * @param {} threshold 
 * @param {} immediate
 */
export function debounce(func, threshold = 500, immediate = false) {
    if (typeof func !== 'function') {
        throw new Error('First argument of debounce function should be a function');
    }
    let timer = null;
    return function debounced(...args) {
        const context = this;
        const callNow = immediate && !timer;
        const later = () => {
            timer = null;
            if (!immediate) func.apply(context, args);
        };
        ////console.log('please wait');
        clearTimeout(timer);
        timer = setTimeout(later, threshold);
        if (callNow) func.apply(context, args);
        // console.log(func);

    };
}


export const formDateMonth = (date, type) => {
    // new Date((1597307920.273 * 1000) - 86400000 *2).toLocaleString()

    date = new Date(date * 1000)
    // let current = (new Date().getTime()) - (86400000 * 1);//24
    let current = new Date(new Date().toLocaleDateString()).getTime();//0
    // console.log(new Date(current))
    let month = (date.getMonth() + 1 + '').length === 1 ? '0' + (date.getMonth() + 1) : date.getMonth() + 1;//
    let day = (date.getDate() + '').length === 1 ? '0' + date.getDate() : date.getDate();//
    let hour = (date.getHours() + '').length === 1 ? '0' + date.getHours() : date.getHours();//
    let minute = (date.getMinutes() + '').length === 1 ? '0' + date.getMinutes() : date.getMinutes();//

    if (date.getTime() < current) {//0
        if (date.getTime() > (current - 86400000)) {//
            if (type === 'showTime') {
                return ` ${hour}:${minute}`
            }
            return ``
        }
        return `${month}-${day}`
        // return `${month}-${day} ${hour}:${minute}`
    } else {
        return `${hour}:${minute}`
    }
}

export const isMobile = (s) => /^1[3-9][0-9]{9}$/.test(s);


export const systemScreen = () => {
    //
    function getDocumentTop() {
        var scrollTop = 0, bodyScrollTop = 0, documentScrollTop = 0;
        if (document.body) {
            bodyScrollTop = document.body.scrollTop;
        }
        if (document.documentElement) {
            documentScrollTop = document.documentElement.scrollTop;
        }
        scrollTop = (bodyScrollTop - documentScrollTop > 0) ? bodyScrollTop : documentScrollTop;
        // console.log("scrollTop:" + scrollTop);
        return scrollTop;
    }

    //
    function getWindowHeight() {
        var windowHeight = 0;
        if (document.compatMode === "CSS1Compat") {
            windowHeight = document.documentElement.clientHeight;
        } else {
            windowHeight = document.body.clientHeight;
        }
        // console.log("windowHeight:" + windowHeight);
        return windowHeight;
    }

    //
    function getScrollHeight() {
        var scrollHeight = 0, bodyScrollHeight = 0, documentScrollHeight = 0;
        if (document.body) {
            bodyScrollHeight = document.body.scrollHeight;
        }
        if (document.documentElement) {
            documentScrollHeight = document.documentElement.scrollHeight;
        }
        scrollHeight = (bodyScrollHeight - documentScrollHeight > 0) ? bodyScrollHeight : documentScrollHeight;
        // console.log("scrollHeight:" + scrollHeight);
        return scrollHeight;
    }
    let obj = {
        getDocumentTop: getDocumentTop(),
        getWindowHeight: getWindowHeight(),
        getScrollHeight: getScrollHeight(),
    }
    return obj
}

export function setCookie(name, value, n) {
    var oDate = new Date();
    oDate.setDate(oDate.getDate() + n);
    document.cookie = name + "=" + value + ";expires=" + oDate;
}

export function getCookie(name) {
    var str = document.cookie;
    var arr = str.split("; ");
    for (var i = 0; i < arr.length; i++) {
        //console.log(arr[i]);
        var newArr = arr[i].split("=");
        if (newArr[0] == name) {
            return newArr[1];
        }
    }
}
export async function getLal(address) {
    //
    geocoder.getLocation(address);
    return new Promise((resolve, reject) => {
        geocoder.setError((err) => {
            message.destroy();
            setTimeout(() => {
                message.error('');
            }, 200);
            resolve(false)
            // console.log(address + '', err)
        })
        //
        geocoder.setComplete((res) => {
            resolve(res.detail)
        })
    });
}