let initState = {
    tabStatus: 1,
    cart: getLocal('cart') || {},
    userStore: getLocal('info') || {},
    shop_id: '1',
    cartSummary: getLocal('summary') || {
        num: 0,
        allPrice: 0,
        coin: 0,
        productList: [],
    },
    defaultAddress: null,
    useAddress: localStorage.getItem('use_address') || '',
    _localtion: null,
    isBrowser: '',
    shop: null,
    code: '',
}
function getLocal(key) {
    if (localStorage.getItem(key)) {
        return JSON.parse(localStorage.getItem(key))
    } else return false
}

export const reducer = (state = initState, action) => {
    let states = JSON.parse(JSON.stringify(state))
    switch (action.type) {
        case "TAB":
            states.tabStatus = action.index
            return states;
        case "INFO": // 
            states.userStore = action.info
            return states;
        case "SHOPID": // 
            states.shop_id = action.id
            return states;
        case "USEADDRESS": // 
            states.useAddress = action.data
            return states;
        case "DEFAULT": // 
            states.defaultAddress = action.data
            return states;
        case "LOCAL": // 
            states._localtion = action._localtion
            return states;
        case "BROWSER": // 
            states.isBrowser = action.text
            return states;
        case "SHOP": // 
            states.shop = action.shop
            return states;
        case "CODE": // code
            states.code = action.code
            return states;
        case 'ADDCART':
            states.cart = cart(action.food, action.shop_id, action.current, state.cart);
            localStorage.setItem('cart', JSON.stringify(states.cart))
            // console.log(states.cart, 'cart');
            return states;
        case 'SETNUM':
            states.cartSummary = action.allSummary; // 
            return states;
        case 'CLEAR':
            states.cart = {};
            states.cartSummary = {
                num: 0,
                allPrice: 0,
                coin: 0,
                productList: [],
            };
            return states;
        default:
            return states;
    }
}



function cart(food, shop_id, current, cart, againNumber) {
    let item = JSON.parse(JSON.stringify(food)); // item
    if (!item.number) { // 
        item.number = 1;
    }
    let setFlag = false; // 
    let list = []; //  list list
    if (cart[shop_id]) { // list  list
        let tempList1 = [...cart[shop_id].list];
        list = tempList1;
    }

    // 
    if (!list[0]) { //  
        list.push({ category_id: item.cate_id, product: [] });
    } else {
        let flag = false; //  
        list.forEach((el) => {
            if (el.category_id === item.cate_id) {
                flag = true;
            }
        });
        if (!flag) { //  
            list.push({ category_id: item.cate_id, product: [] });
        }
    }


    // 
    list.forEach((el, index) => { // el ->>> 
        let e = item;
        if (item.cate_id === el.category_id) { // ---> id===id
            if (!el.product[0]) { //    
                list[index].product.push(e); // product
            } else { //    current
                let flag = false; //  true-> false-> currentType
                el.product.forEach((el2, i) => { // product[]
                    if (el2.product_id === item.product_id) { //  currentType
                        flag = true;
                        if (current === 'add' && !againNumber) { // +1
                            list[index].product[i].number += 1;
                        } else if (current === 'del') {
                            list[index].product[i].number -= 1;
                            //
                            if (list[index].product[i].number === 0) {  // 0  
                                list[index].product[i].number = 0
                                list[index].product.splice(i, 1);
                                if (!el.product[0]) { //  
                                    list.splice(index, 1);
                                }
                                if (!list[0]) { // list del
                                    console.log('');
                                    setFlag = true;
                                }
                            }
                        }
                        // if (againNumber) { //  number 
                        //     list[index].product[i].number = againNumber;
                        // }
                    }
                });
                if (!flag) { //  push1
                    list[index].product.push(e);
                }
            }
            // if (list[index]) {
            //     list[index].user_coupon_id = ''; // 
            // }
        }
    });

    if (cart[shop_id]) { //  list
        cart[shop_id] = { ...cart[shop_id], list };
    } else {
        const shopObj = {
            shop_id, // id
            list, // list-> {  } -> { product-list }
            // user_coupon_id: '', // 
            // table_id: '',
            // member_id: '',
            // couponNum: null,
        };
        cart[shop_id] = shopObj; // 
    }
    if (setFlag) { // list 
        delete cart[shop_id];
    }
    return cart
}