import React from 'react'
import { mapStateToProps, mapDispatchToProps } from '../redux/actionCreator'
import { connect } from 'react-redux'
import { useState, useEffect } from 'react'
import np from 'number-precision'
import cartIcon from '../assets/icon/addCart.png'
import './Cart_input.scss'
import { message } from 'antd'

function Input(props) {
    const [num, setNum] = useState(0)
    let {
        food_item,
        addCart,
        isShowIcon,
        isCashier,
    } = props;

    function cartFn(food, shop_id, current) {
        if (isCashier) {
            if (props.cartSummary.num === 1 && current === 'del') {
                message.info('')
                return
            }
        }
        if (num === 0 && current === 'del') { // 0  
            return
        }
        addCart(food, shop_id, current);
        foodNum(food);
    }
    useEffect(() => {
        foodNum(food_item);
        // eslint-disable-next-line
    }, []) // cartBar

    // useEffect(() => {
    //     // foodNum(food_item)
    //     // eslint-disable-next-line
    // }, [props.cartSummary?.num]) // cartBar

    const foodNum = (food) => {
        let flag = false;
        let allNumber = 0;
        let allPrice = 0;
        let coin = 0;
        let productList = []
        let allCartComputer = {
            num: 0,
            coin: 0,
            allPrice: 0,
            productList: [],
        };
        if (props.cart) { // 
            if (Object.keys(props.cart)[0]) {
                Object.values(props.cart)[0].list.forEach(e => { // 
                    e.product.forEach(el => { // 

                        allNumber += el.number; // 
                        allPrice += np.times(el.number, el.sell_price); // 
                        coin += np.times(el.number, el.coin_price); // 
                        productList.push(el); // 

                        if (el.product_id === food.product_id) {
                            flag = true;
                            if (el.number) { // input
                                setNum(el.number)
                            } else {
                                setNum(0)
                            }
                        }
                    })
                });
            } else {
                setNum(0)
            }
            if (!flag) { //  0
                setNum(0)
            }

            //  allCartComputer
            allCartComputer.num = allNumber; // 
            allCartComputer.allPrice = allPrice; // 
            allCartComputer.productList = productList; // 
            allCartComputer.coin = coin; // 
            props.setAllNum({ ...allCartComputer }); // 
            localStorage.setItem('summary', JSON.stringify({ ...allCartComputer })); // 
        }
    }

    return (
        <div className='cart_input_wrap' onClick={() => { !num && cartFn(food_item, props.shop_id, 'add') }} >
            {
                num
                    ? <>
                        <i className="btn btn-minus" onClick={() => { cartFn(food_item, props.shop_id, 'del') }}></i>
                        <span>{num}</span>
                        <i className="btn btn-plus" onClick={() => { cartFn(food_item, props.shop_id, 'add') }}></i>
                    </>
                    : !isShowIcon
                        ? <>
                            <i className="btn btn-minus" onClick={() => { cartFn(food_item, props.shop_id, 'del') }}></i>
                            <span>{num}</span>
                            <i className="btn btn-plus" onClick={() => { cartFn(food_item, props.shop_id, 'add') }}></i>
                        </>
                        : <>
                            <img src={cartIcon} alt='a' />
                        </>
            }

        </div>
    )
}
export default connect(mapStateToProps, mapDispatchToProps)(Input)