import React, { useEffect, useState, useRef } from 'react'
import { mapStateToProps, mapDispatchToProps } from '../../redux/actionCreator'
import { connect } from 'react-redux'
import { useHistory } from 'react-router-dom'
import { Tabs, Button, message } from 'antd';
import np from 'number-precision';
import { debounce } from '../../utils/utils'
import { orderListApi } from '../../api/shopApi'
import './order.scss';

const { TabPane } = Tabs;
function Table(props) {
    const listDOM = useRef();
    const wrapDOM = useRef();
    const [loading, setLoading] = useState(false);
    const [itemLoading, setItemLoading] = useState(null);
    const [listHeight, setListHight] = useState(0);
    const [wrapHeight, setWrapHight] = useState(0);

    const [type, setType] = useState('');
    const [page, setPage] = useState(1);
    const [total, setTotal] = useState(0);


    const history = useHistory();
    const [list, setList] = useState([]);


    async function init() {
        message.loading({ content: '', duration: 0 })
        let res = await orderListApi('2', 1);
        if (res) {
            setTotal(res.total);
            message.destroy();
            setList(res.list)
            if (!res.list[0]) { message.info('') }
        }
    }

    const upOrderItemFn = async (e) => {
        setItemLoading({
            loading: true,
            order_id: e.order_id,
        });
        setTimeout(() => {
            setItemLoading(null);
        }, 500);
    }
    // 
    const scrollFn = debounce(async (h) => {
        console.log(list.length, total);

        if (h === 0) {
            console.log('');
            if (total === list.length) {
                message.warn('');
                return
            }
            if (total > 10 && list.length !== total) {
                message.loading({ content: '', duration: 0 })
                let res = await orderListApi(type, page + 1);
                if (res) {
                    setList([...list, ...res.list]);
                    setPage(page + 1);
                    setTotal(res.total);
                    message.destroy();
                    message.success('');
                }
            }
        }
    }, 400);

    function statusFn(e) {
        // 1  2  3  4  5  6  7 8
        if (e.status === 1) return <span style={{ color: '#F97B50' }}></span>
    }

    async function tabFn(e) {
        setType(e);
        message.loading({ content: '', duration: 0 })
        let res = await orderListApi(e, 1);
        if (res) {
            setTotal(res.total);
            setList(res.list);
            message.destroy();
            if (!res.list[0]) { message.info('') }
        }
    }

    function jump(order_id) {
        console.log('' + order_id);
        history.push(`/integral/orderdetail?order_id=${order_id}`)
    }

    useEffect(() => { setListHight(listDOM.current.clientHeight) }, [list]);

    useEffect(() => {
        setWrapHight(wrapDOM.current.clientHeight);
        setListHight(listDOM.current.clientHeight);
        init();
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [])


    return (
        <div className="order_wrap animate__fadeIn animate__animated" >
            <Tabs defaultActiveKey="1" onChange={(e) => { e && tabFn(e) }} className='order_tabs'>
                {/* 1  2  3  4  5  6 7 8  */}
                <TabPane tab="" key="2" />
                <TabPane tab="" key="3" />
                <TabPane tab="" key="6" />
                <TabPane tab="" key="8" />
            </Tabs>
            <div className="list_wrap" ref={wrapDOM} onScroll={(e) => {
                scrollFn(listHeight - wrapHeight + 12 - e.target.scrollTop)
            }}>
                <div ref={listDOM}>
                    {
                        list && list.map((e, i) => {
                            return (
                                <div className='order_item' key={e.order_id + '' + e.status} onClick={() => { jump(e.order_id) }}>
                                    <div className='img_box'>
                                        <img src={e.shop.logo} alt="a" />
                                    </div>
                                    <div className='content'>
                                        <div className='shop_name'>
                                            {e.shop.shop_name}
                                        </div>
                                        {
                                            e.distribution_mode === 1
                                                ? <>
                                                    <div className='address'><span></span>{e.address.address}</div>
                                                    <div className='time'><span></span>{e.delivery_time || ''}</div>
                                                </> : <div className='ziti'><span></span>{e.shop.address}</div>
                                        }
                                        <div className={e.distribution_mode === 1 ? 'price' : 'tprice'}>
                                            {e.type_message === '' ? <><span></span>{e.price}</> : <><span></span>{e.price}</>}

                                        </div>
                                    </div>
                                    <div className='status' style={{ color: '#F97B50' }}>
                                        {e.status_message}
                                    </div>
                                    {/* <Button className='upBtn' loading={itemLoading ? (itemLoading.order_id === e.order_id ? itemLoading.loading : false) : false}
                                        onClick={(event) => { event.stopPropagation(); upOrderItemFn(e) }}
                                    >
                                        
                               </Button> */}
                                </div>
                            )
                        })
                    }
                </div>
            </div>
        </div>
    )
}
export default connect(mapStateToProps, mapDispatchToProps)(Table)