// 
/**
 * 
 * @param {*} list 
 * @param {*} method 567
 * @param {*} address_id id
 * @param {*} type --12
 */
export const createOrderApi = async (list, method, address_id, type) => {
    let address = '';
    if (type === 1) address = address_id;
    const result = await request({
        method: 'post',
        url: '/shop/v1/order/create',
        data: { list, method, address_id: address, type },
    })
    return result;
}
/**
 * 
 * @param {*} method 567
 * @param {*} order_id order_id
 */
export const payApi = async (method, order_id, code) => {
    console.log(code);

    let data = {};
    if (method == 7) {
        data = { method, order_id, }
    } else {
        data = { method, order_id, code }
    }

    const result = await request({
        method: 'post',
        url: '/shop/v1/order/pay',
        data,
    })
    return result;
}



